body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.App.dark {
  background-color: #111c2d;
  color: white;
}

.App.light {
  background-color: #ffffff;
  color: black;
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1e2a3a;
  border-bottom: 1px solid #2a2a2a;
  z-index: 1000;
  flex-wrap: wrap;
}

/* Logo */
.logo {
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  color: inherit;
}

/* Light / Dark navbar colors */
.App.dark .logo,
.App.dark .nav-links a,
.App.dark .theme-toggle {
  color: white;
}

.App.light .logo,
.App.light .nav-links a,
.App.light .theme-toggle {
  color: black;
}

.App.light .navbar {
  background-color: #f5f5f5;
}

/* Right side of nav */
.nav-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

/* Nav links */
.nav-links {
  display: flex;
  gap: 30px;
  align-items: center;
  flex-wrap: wrap;
}

.nav-links a {
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
  color: inherit;
}

.nav-links a:hover {
  opacity: 0.85;
}

/* Theme toggle button */
.toggle-container {
  margin-right: 10px;
}

.theme-toggle {
  border: none;
  background: transparent;
  font-size: 16px;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 5px;
  color: inherit;
}

/* Main content area */
.main-content {
  padding-top: 100px;
  min-height: 100vh;
}

/* Page layout */
.page {
  padding: 40px 20px;
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.7;
  text-align: left;
}

.page h2 {
  font-size: 32px;
  margin-bottom: 20px;
}

/* Footer (icon links) */
.footer {
  position: fixed;
  bottom: 20px;
  left: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  z-index: 999;
}

.footer a {
  color: inherit;
  transition: color 0.3s ease;
}

.footer a:hover {
  color: #00bfff;
}

/* Responsive tweaks */
@media (max-width: 768px) {
  .nav-links {
    flex-wrap: wrap;
    gap: 15px;
  }

  .toggle-container {
    margin-top: 10px;
  }

  .nav-right {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .navbar {
    padding: 20px 20px;
  }
}
